import React from 'react'
import './stocks.scss'
import back from '../assets/img/blank-1080.jpg'
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos'
import FormControl from '@material-ui/core/FormControl'
import Select from '@material-ui/core/Select'
import InputLabel from '@material-ui/core/InputLabel'
import MenuItem from '@material-ui/core/MenuItem'
import {makeStyles} from '@material-ui/core/styles'
import {Link} from 'react-router-dom'
import OneStock from './OneStock'
import Pagination from '../components/pagination'
import httpService from "../services/Http.service";
import TextField from "@material-ui/core/TextField";

const useStyles = makeStyles(theme => ({
    formControl: {
        margin: theme.spacing(1),
        width: '100%'
    },
    selectEmpty: {
        marginTop: theme.spacing(2),
    },
}))
export default function Stocks() {
    const classes = useStyles()
    const [age, setAge] = React.useState('')
    const [filter, setFilter] = React.useState('')
    const [page, setPage] = React.useState(0)
    const [count, setCount] = React.useState(0)
    const [data, setData] = React.useState([])
    const handleChange = event => {
        setAge(event.target.value)
    }
    React.useEffect(() => {
        httpService.httpGet(`company/getAll?page=${page}${(filter.length>0 ? '&filter=' + filter : '')}`).then(res => {
            setData(res.docs);
            setCount(res.totalDocs)
          console.log(Math.ceil(res.totalDocs/10))
        })
    }, [filter, page]);
    const changePage = (event, value) => {
      console.log(value);
        setPage(value-1);
    };
    const onChangeFilter = (key, value) => {
        setFilter( value );
        setPage(0);
    };
    return (
        <div className='container' style={{marginTop: '67px'}}>
            <div className='search'>
                <div className='search-container'>
                    <div className='overlay'></div>
                    <img src={back} className='back-img' alt={'back'}/>
                </div>

                <div className='filter-content'>
                    <ul className='breadcrumb'>
                        <li>
                            Stocks
                            <ArrowBackIosIcon/>

                        </li>
                        <li>
                            Global
                        </li>
                    </ul>

                    <div className='col-md-12'>
                        <div className='row'>
                            <h1>
                                Global Stocks
                            </h1>
                        </div>
                    </div>

                    <div className='col-md-12'>
                        <div className='row'>
                            <div className='col-md-3'>
                                <FormControl className={classes.formControl}>
                                    <TextField id="standard-basic" label="نماد یا نام شرکت" className='w-100'
                                               onChange={e => onChangeFilter('symbolFA', e.target.value)}
                                    />
                                </FormControl>
                            </div>

                            {/*<div className='col-md-3'>*/}
                            {/*    <FormControl className={classes.formControl}>*/}
                            {/*        <TextField id="standard-basic" label="نام شرکت" className='w-100'*/}
                            {/*                   onChange={e => onChangeFilter('nameFA', e.target.value)}*/}
                            {/*        />*/}
                            {/*    </FormControl>*/}
                            {/*</div>*/}

                            <div className='col-md-3'>
                                <FormControl className={classes.formControl}>
                                    <InputLabel id="demo-simple-select-label">صنعت</InputLabel>
                                    <Select
                                        labelId="demo-simple-select-label"
                                        id="demo-simple-select"
                                        value={age}
                                        onChange={handleChange}
                                    >
                                        <MenuItem value={10}>Ten</MenuItem>
                                        <MenuItem value={20}>Twenty</MenuItem>
                                        <MenuItem value={30}>Thirty</MenuItem>
                                    </Select>
                                </FormControl>
                            </div>

                            <div className='col-md-3'>
                                <div className='row align-items-center filter justify-content-end h-100'>
                                    <Link to={'screener'}>
                                        فیلتر های بیشتر
                                    </Link>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <div className='col-md-12 mt-3'>
                <div className='row'>
                    {data.map((item, index) => {
                        return (
                            <div className='col-md-3' style={{marginTop: '10px'}}>
                                {/*<div className='row'>*/}
                                <OneStock data={item}/>
                                {/*</div>*/}
                            </div>
                        )
                    })}
                </div>
            </div>

            <div className='col-md-12 mt-5 mb-5'>
                <Pagination page={page} count={count} changePage={changePage}/>
            </div>
        </div>
    )
}
