import React from 'react';
import './stocks.scss'
import boxImg from '../assets/img/logo.jpg'
import {Link} from 'react-router-dom'

export default function OneStock(props) {
    const data = props.data;
    return (
        <div className='each-stock'>
            <div onClick={() => localStorage.setItem('id', data._id)}>
                <Link to={'main/dashboard?id=' + data._id}>
                    <div className='top-box'>
                        <div className='top-box-overlay'></div>
                        <img src={data.avatar ? data.avatar : boxImg} alt={'avatar'} className='box-img'/>
                    </div>
                    <div className='chart-container'>
                        <div className='chart'>
                            {/*<RadarDiagramD3/>*/}
                        </div>
                    </div>

                    <div className='more-info'>
                        <div className='stock-name'>
                            <div className='name-container'>
                                <h3>
                                    {data.nameFA}
                                </h3>
                                <h3 className='type'>
                                    {data.name}
                                </h3>
                                <h3 className='type'>
                                    #{data.symbolFA}
                                </h3>
                            </div>
                        </div>
                    </div>
                </Link>

            </div>
        </div>
    )
}
