import axios from 'axios';
import { URL as BASE_URL} from '../config/Http'



const httpService = {

    async httpGetWithHeader(url, headers) {
        const res = await axios.get(BASE_URL + url, { headers });
        return res;
    },
    async httpGet(url) {
        const res = await axios.get(BASE_URL + url);
        return res.data;
    },
    httpPost(url, data) {
        return new Promise((resolve, reject) => {
            var headers = {
                'Content-Type': 'application/json',
                'Accept': 'application/json'
            }

            axios.post(BASE_URL + url, data, { headers: headers })
                .then(function (response) {
                    resolve(response.data)
                })
                .catch(function (error) {
                    console.log(22, error);
                })
        })
    },
    httpPostJWT(url, data) {
        return new Promise((resolve, reject) => {
            var headers = {
                'Content-Type': 'application/json',
                'Accept': 'application/json',
                'Authorization':'Bearer '+localStorage.getItem('token')
            }

            axios.post(BASE_URL + url, data, { headers: headers })
                .then(function (response) {
                    resolve(response.data)
                })
                .catch(function (error) {
                    console.log(22, JSON.stringify(error));
                })
        })
    },
    async httpPutJwt(url, data) {
        // const token = await storageService.getItem('token');
        console.log('token ', localStorage.getItem('token'))
        const AuthStr = 'Bearer ' + localStorage.getItem('token');
        var headers = { 'Content-Type': 'application/json', 'Authorization': AuthStr }
        const res = await axios.put(BASE_URL + url, data, { headers: headers });
        return res.data;
    },
    async upload(url, data, contentType) {
        // const token = await storageService.getItem('token');
        const AuthStr = 'Bearer ' + localStorage.getItem('token');
        var headers = { 'Content-Type': contentType, 'Authorization': AuthStr }
        const res = await axios.post(BASE_URL + url, data, { headers: headers });
        return res;
    },

    async httpGetJwt(url) {
        const AuthStr = 'Bearer ' + localStorage.getItem('token');
        var headers = { 'Content-Type': 'application/json', 'Authorization': AuthStr }
        const res = await axios.get(BASE_URL + url, { headers: headers });
        return await res.data;
    },
    async httpDeleteJwt(url) {
        const AuthStr = 'Bearer ' + localStorage.getItem('token');
        var headers = { 'Content-Type': 'application/json', 'Authorization': AuthStr }
        const res = await axios.delete(BASE_URL + url, { headers: headers });
        return await res;
    }
};

export default httpService;
